import Salada from "../../assets/salad.jpg";
import Carne from "../../assets/meat.jpg";
import Peixe from "../../assets/fish.jpg";
import Pasta from "../../assets/ragu-5.jpg";

import EnotecaVinhos from "../../assets/Enoteca-vinhos.jpg";

import "./rosticeria.scss";

const Rosticeria = () => {
  return (
    <div className="section-rosticeria">
      <div className="rosticeria-container">
        <div className="rosticeria-header">
          <span>Rosticceria</span>
        </div>
        <div className="rosticeria-meals">
          <div className="rosticeria-meals-text">
            <h1>Nossos Pratos</h1>
            <p>
              Entre em contato para fazer o seu pedido imediato ou encomendas
              programadas.
            </p>
            <p>&nbsp;</p>
            <p>
              Lembrando que estamos com vocs para todas as datas celebrativas
              como: Pscoa, Natal, Pessah, Dia das Mes, Dia dos Pais e
              recomendamos fazer as encomendas com antecedncia!
            </p>
            <p>&nbsp;</p>
            <p>Confira nosso cardpio.</p>
          </div>
          <div className="rosticeria-meals-image">
            <img src={Pasta} alt="pasta" />
          </div>
        </div>
        <div className="rosticeria-dicas">
          <div className="dicas-header">
            <span className="dicas-heading">Dicas de Preparo</span>
            <span className="dicas-text">
              Preparamos algumas dicas simples e prticas para que voc tenha
              uma excelente refeio.
            </span>
          </div>
          <div className="dicas-items">
            <div className="dicas-item">
              <div className="dicas-item-image">
                <img src={Salada} alt="example" />
              </div>
              <div className="dicas-item-text">
                <h1>Saladas</h1>
                <p>
                  Sempre devero ser guardadas no refrigerador at a hora de
                  servir. As saladas de alcachofra, berinjela assada e muitas
                  outras da Paola podem ser servidas junto com saladas de folhas
                  para enriquecer ainda mais a mesa.
                </p>
              </div>
            </div>
            <div className="dicas-item">
              <div className="dicas-item-text">
                <h1>Carnes e Aves ao Molho</h1>
                <p>
                  Levar ao forno pr aquecido  150C durante uns 15 a 20
                  minutos e coberto com papel de alumnio.
                </p>
              </div>
              <div className="dicas-item-image">
                <img src={Carne} alt="example" />
              </div>
            </div>
            <div className="dicas-item">
              <div className="dicas-item-image">
                <img src={Peixe} alt="example" />
              </div>
              <div className="dicas-item-text">
                <h1>Peixes</h1>
                <p>
                  Cobrir com papel alumnio e levar ao forno pr aquecido 
                  180C durante 15 minutos.
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="rosticeria-massas">
          <div className="rosticeria-massas-text">
            <h1>Massas</h1>
            <p>
              <b>Forno</b>: Coloque uma camada de molho no fundo de uma travessa
              refratria e distribua a massa por cima garantindo que haja molho
              entre elas. Coloque mais molho por cima, espalhe queijo parmeso
              ralado se quiser e cubra com papel de alumnio. Aquea durante
              20min. em forno mdio (180C) preaquecido. Tire o papel alumnio.
              e leve novamente ao forno descoberto e deixe gratinar mais uns 5
              min.
            </p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>
              <b>Escaldada</b>: Escalde a massa em gua fervente por 1 a 2
              minutos aproximadamente. Aquea o molho separadamente. Escorra a
              massa com ajuda de uma escumadeira ou um coador prprio para
              massa. Disponha a massa no prato ou na travessa que for servir.
              Cubra com o molho quente e na quantidade a seu gosto ou leve 
              mesa em separado. Espalhe queijo parmeso se desejar.
            </p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>
              <b>Microondas</b>: Coloque uma camada de molho no fundo de um
              prato prprio para micro-ondas. Distribua a massa, cubra com molho
              e aps com filme prprio para este uso. Perfure o filme. leve ao
              forno de micro-ondas em potncia mdia por dois minutos. Embora
              no seja recomendado o uso de microondas para aquecer e cozinhar
              massas, em alguns casos pode ser uma opo em funo da
              praticidade e da rapidez.
            </p>
          </div>
          <div className="rosticeria-massas-image"></div>
        </div>
      </div>
    </div>
  );
};

export default Rosticeria;
