import { Link } from "react-router-dom";

import Tomato from "../../assets/videos/tomato.mp4";

import "./sobre-nos.scss";

const SobreNos = () => {
  return (
    <div className="section-sobre-nos">
      <div className="sobre-nos-container">
        <div className="sobre-nos-video">
          <video src={Tomato} autoPlay loop muted />
        </div>
        <div className="sobre-nos-text">
          <h1>Paixo</h1>
          <p>
            Cada criao  tratada como uma obra nica desenhada com carinho.
          </p>
          <p>&nbsp;</p>
          <p>
            Elevamos a definio de "massa" a um nvel nunca explorado, cada
            item exala sofisticao.
          </p>
          <p>&nbsp;</p>
          <p>
            Fazemos a fuso das tradies centenrias da famlia italiana com
            todas as inovaes da culinria contempornea unindo o antigo ao
            novo para criar uma experincia de encantamento inesperado para o
            paladar.
          </p>
          <p>&nbsp;</p>
          <p>
            Nosso trabalho se destaca pela qualidade dos ingredientes, o cuidado
            na preparao e sofisticao nos sabores.
          </p>
          <p>&nbsp;</p>
          <p>
            Nossos recheios se apresentam sutilmente, surpreendendo a cada
            nuance, tomando forma lentamente em uma experincia sensorial
            inesquecvel.
          </p>
        </div>
      </div>
      <div className="enoteca-preview-container">
        <div className="enoteca-text">
          <h1 className="enoteca-heading">Enoteca</h1>
          <span className="enoteca-description">
            Descubra os prazeres da combinao de uma boa massa com um bom
            vinho.
          </span>
          <button className="enoteca-button">
            <Link
              className="enoteca-link"
              to="/enoteca"
              onClick={() => window.scrollTo(0, 0)}
            >
              <span>Saiba Mais</span>
            </Link>
          </button>
        </div>
      </div>
    </div>
  );
};

export default SobreNos;
